  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 25px;
}
@media (max-width: 1024px) {
  .reviews-card__header {
    margin-bottom: 15px;
  }
}
.reviews-card__user {
  display: flex;
  align-items: center;
}
.reviews-card__user-avater {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 44px;
  height: 44px;
  border-radius: 30px;
  background-color: var(--main);
}
.reviews-card__user-name {
  margin-left: 15px;
}
.reviews-card__img {
  height: 60px;
  width: 100%;
}
.reviews__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 80px;
}
.reviews__nav-btn {
  display: inline-flex;
  position: static;
}
.reviews__nav-btn--prev {
  transform: scale(-1, 1);
}
.reviews__nav-btn svg {
  flex-shrink: 0;
  width: 35px;
  height: 35px;
  border: 2px solid var(--dark);
  border-radius: 100%;
  fill: none;
  background-color: transparent;
  stroke: var(--dark);
  stroke-linecap: round;
  stroke-width: 2px;
  transition: stroke 0.2s ease-in-out, border 0.2s ease-in-out, background-color 0.2s ease-in-out;
}
.reviews__nav-btn:hover svg {
  background-color: var(--dark);
  stroke: var(--light);
}
.reviews__nav-btn:focus {
  outline: none;
}
.reviews__nav-btn:focus svg {
  stroke: var(--dark);
  background-color: var(--accent);
}

.feedback {
  background-image: linear-gradient(#202124, #202124);
  background-repeat: no-repeat;
  background-size: 100% 50%;
  background-position: bottom;
}
.feedback__container {
  padding-bottom: 80px;
}
@media (max-width: 1024px) {
  .feedback__container {
    padding-bottom: 60px;
  }
}
.feedback__wrapp {
  display: flex;
  background-color: var(--light);
  border-radius: 25px 50px 25px 25px;
}
@media (max-width: 768px) {
  .feedback__wrapp {
    flex-direction: column;
  }
}
.feedback__content {
  width: 60%;
  padding: 40px;
}
@media (max-width: 768px) {
  .feedback__content {
    width: 100%;
  }
}
@media (max-width: 1024px) {
  .feedback__content {
    padding: 25px;
  }
}
.feedback__title {
  margin-bottom: 20px;
}
.feedback__text {
  margin-bottom: 15px;
}
.feedback__link {
  display: inline-block;
}
@media (max-width: 576px) {
  .feedback__link {
    display: block;
    text-align: center;
  }
}
.feedback__img-wrapp {
  width: 40%;
}
@media (max-width: 768px) {
  .feedback__img-wrapp {
    width: 100%;
    padding: 0 25px 25px;
  }
}
.feedback__image {
  height: 100%;
  border-radius: 0 50px;
}
@media (max-width: 768px) {
  .feedback__image {
    border-radius: 25px;
  }
}

.footer {
  background-color: var(--dark);
  padding-top: 60px;
  padding-bottom: 60px;
}
.footer__container {
  display: grid;
  grid-template-columns: 1fr 1fr auto;
  gap: 30px;
  color: var(--light);
}
@media (max-width: 768px) {
  .footer__container {
    grid-template-columns: 1fr auto;
  }
}
.footer__top {
  grid-column: 1/span 3;
  display: inline-flex;
}
@media (max-width: 768px) {
  .footer__top {
    grid-column: 1/span 2;
  }
}
.footer__contacts {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}
.footer__contacts-item {
  font-weight: 600;
}
.footer__contacts-item:not(:last-child) {
  margin-bottom: 5px;
}
.footer__link {
  color: var(--light);
  transition: color 0.2s ease-in-out;
}
.footer__link:hover {
  color: var(--accent);
}
.footer__link:focus {
  outline: none;
  color: var(--accent);
}
.footer__nav {
  width: 300px;
  display: flex;
  flex-direction: column;
  max-height: 150px;
  flex-wrap: wrap;
}
.footer__nav-item:not(:last-child) {
  margin-bottom: 10px;
}
@media (max-width: 768px) {
  .footer__nav {
    display: none;
  }
}